header {
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  background: black;
  width: 99%;
  cursor: pointer;
  text-decoration: none;
  z-index: +1;
  border: 0.2rem solid #fff;
    border-radius: 2rem;
    box-shadow: 0 0 .2rem #fff,
              0 0 .2rem #fff,
              0 0 2rem yellow,
              0 0 0.8rem yellow,
              0 0 2.8rem yellow,
              inset 0 0 1.3rem yellow;
  margin-bottom: 0px;
  margin-top: 10px;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2.8em;
  color: white;
  text-shadow:
    0 0 7px #fff,
    0 0 10px #fff,
    0 0 21px #fff,
    0 0 42px #bc13fe,
    0 0 82px #bc13fe,
    0 0 92px #bc13fe,
    0 0 102px #bc13fe,
    0 0 151px #bc13fe;
}

.homeBtn {
  border:none;
  background:none;
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 2.2em;
  }
}